{#if tile}
  <label class="paper-switch-tile"
         class:disabled>
    <input bind:checked
           type="checkbox"
           {disabled} />
    <div class="paper-switch-tile-card border">
      <div class={`paper-switch-tile-card-front border
                   ${bgClass(tile.off.background)}`}>
        {tile.off.label}
      </div>
      <div class={`paper-switch-tile-card-back border
                   ${bgClass(tile.on.background)}`}>
        {tile.on.label}
      </div>
    </div>
  </label>
{:else}
  <label class={prefix}>
    <input id={domId}
           bind:checked
           type="checkbox"
           {disabled} />
    <span class="paper-switch-slider"
          class:round></span>
  </label>
  {#if $$slots.default}
    <label class={`${prefix}-label`}
           for={domId}>
      <slot />
    </label>
  {/if}
{/if}

<script lang="ts" context="module">let count = 0;
</script>

<script lang="ts">import { onMount } from "svelte";
;
export let checked = false;
export let disabled = false;
export let round = false;
export let inline = false;
export let tile = null;
let id;
onMount(() => {
    id = count++;
});
let domId;
$: domId = `paper-switch-input-${id}`;
let prefix;
$: prefix = 'paper-switch' + (inline ? '-2' : '');
function bgClass(color) {
    if (!color)
        return;
    return `background-${color}`;
}
</script>

<style lang="scss">input[disabled] + span,
.paper-switch-tile.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}</style>
